import React, { useContext, useEffect, useState } from "react";
import swal from "sweetalert";
import { useHistory } from 'react-router-dom';
import axios from 'axios';
import { LoginContext } from "./providers/LoginProviders";
import { Button, Card, CardBody, ChakraProvider, Input } from "@chakra-ui/react";
import styled from "styled-components";
import logo from "../assets/images/logo.JPG"

function Login() {
    const { setIsLogin } = useContext(LoginContext);
    const history = useHistory();

    const moveRegister = () => {
        history.push('/register');
    };

    const [loginInput, setLogin] = useState({
        email: '',
        password: '',
        error_list: [],
    });

    const handleInput = (e) => {
        e.persist();
        setLogin({...loginInput, [e.target.name]: e.target.value});
    }

    const loginSubmit = (e) => {
        e.preventDefault();

        const data = {
            email: loginInput.email,
            password: loginInput.password,
        }

        axios.get('/sanctum/csrf-cookie').then(response => {
            // console.log(response);
            axios.post(`api/login`, data).then(res => {
                console.log(res);
                if(res.data.status === 200){
                    if(res.data.type === 'student'){
                        history.push('/');
                    } else {
                        history.push('/admin');
                    }
                    localStorage.setItem('auth_token', res.data.token);
                    localStorage.setItem('auth_name', res.data.username);
                    setIsLogin(true);
                    swal("", res.data.message, "success");
                } else if (res.data.status === 401){
                    swal("", res.data.message, "warning");
                } else {
                    setLogin({...loginInput, error_list: res.data.validation_errors});
                }
            });
        });
    }

    return (
        <ChakraProvider>
            <SContainer>
            <div className="row justify-content-center">
                <div className="col-md-6 col-lg-6 mx-auto">
                    <SImg>
                        <img width="400" height="400" src={logo} alt="" />
                    </SImg>
                    <Card maxW='sm'  margin="auto" marginTop={10}>
                        <CardBody>
                        <SCardHeader>
                            <h4></h4>
                        </SCardHeader>
                        <div className="card-body">
                            <form onSubmit={loginSubmit}>
                                <div className="form-group mb-3">
                                    <label></label>
                                    <Input type="email" name="email" onChange={handleInput} value={loginInput.email} className="form-control" focusBorderColor="green.700" />
                                    <span>{loginInput.error_list.email}</span>
                                </div>
                                <div className="form-group mb-3">
                                    <label></label>
                                    <Input type="password" name="password" onChange={handleInput} value={loginInput.password} className="form-control" focusBorderColor="green.700" />
                                    <span>{loginInput.error_list.password}</span>
                                </div>
                                <div className="form-group mb-3">
                                    <Button type="submit" marginTop={3} marginRight={2} color="green.700"></Button>
                                    <Button type="submit" marginTop={3} onClick={moveRegister} color="green.700"></Button>
                                </div>
                            </form>
                        </div>
                        </CardBody>
                    </Card>
                </div>
            </div>
        </SContainer>
        </ChakraProvider>
    );
}

const SCardHeader = styled.div`
margin-bottom: 10px;
    }
    `

const SContainer = styled.div`
background-color: white;
height: 100vh;
`

const SImg =styled.div`
padding-left: 36%;
padding-top: 200px;
`



export default Login;
